<template>
  <view class="page">
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <view class="header">
      <view class="back-btn" @click="goBack">
        <text class="back-icon">‚Äπ</text>
        <text class="back-text">ËøîÂõû</text>
      </view>
      <text class="title">Áî®Êà∑ÁÆÄ‰ªã</text>
      <view class="placeholder"></view>
    </view>

    <!-- Áî®Êà∑‰ø°ÊÅØÂç°Áâá -->
    <view class="user-card">
      <view class="user-info">
        <!-- Â§¥ÂÉè -->
        <image class="avatar" :src="userInfo.avatar || '/static/picture/profile.png'" mode="aspectFill" @error="onAvatarError" />
        
        <!-- Áî®Êà∑ÂêçÂíå‰∏™‰∫∫ÁÆÄ‰ªã -->
        <view class="user-details">
          <text class="username">{{ userInfo.name || 'ÁæéÈ£üËææ‰∫∫' }}</text>
          <text class="bio">{{ userInfo.bio || 'Ëøô‰∏™‰∫∫ÂæàÊáíÔºåËøòÊ≤°ÊúâÂÜôÁÆÄ‰ªã~' }}</text>
        </view>
      </view>

      <!-- ÁªüËÆ°Êï∞ÊçÆ -->
      <view class="stats">
        <view class="stat-item" @click="showFollowList">
          <text class="stat-number">{{ userInfo.followCount || 0 }}</text>
          <text class="stat-label">ÂÖ≥Ê≥®</text>
        </view>
        <view class="divider"></view>
        <view class="stat-item" @click="showFansList">
          <text class="stat-number">{{ userInfo.fansCount || 0 }}</text>
          <text class="stat-label">Á≤â‰∏ù</text>
        </view>
        <view class="divider"></view>
        <view class="stat-item" @click="showLikesList">
          <text class="stat-number">{{ userInfo.likeCount || 0 }}</text>
          <text class="stat-label">Ëé∑Ëµû</text>
        </view>
      </view>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <view class="actions" v-if="!isCurrentUser">
        <button class="follow-btn" :class="{ 'followed': userInfo.isFollowed }" @click="toggleFollow">
          {{ userInfo.isFollowed ? 'Â∑≤ÂÖ≥Ê≥®' : 'ÂÖ≥Ê≥®' }}
        </button>
        <button class="message-btn" @click="sendMessage">ÁßÅ‰ø°</button>
      </view>
    </view>

    <!-- Â∏ñÂ≠êÂàóË°® -->
    <view class="posts-section">
      <view class="section-title">
        <text class="title-text">‰ΩúÂìÅÂàóË°®</text>
        <text class="posts-count">({{ posts.length }})</text>
      </view>

      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <view class="loading-state" v-if="loading">
        <view class="loading-spinner"></view>
        <text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-else-if="!loading && posts.length === 0">
        <image class="empty-icon" src="/static/picture/profile.png" mode="aspectFit"></image>
        <text class="empty-title">ÊöÇÊó†‰ΩúÂìÅ</text>
        <text class="empty-desc">Ëøô‰∏™Áî®Êà∑ËøòÊ≤°ÊúâÂèëÂ∏É‰ªª‰Ωï‰ΩúÂìÅ</text>
      </view>

      <!-- Â∏ñÂ≠êÂàóË°® -->
      <view class="posts-list" v-else>
        <view class="post-item" v-for="post in posts" :key="post.id" @click="viewPostDetail(post)">
          <view class="post-content">
            <text class="post-text">{{ post.content || 'Êó†ÊñáÂ≠óÂÜÖÂÆπ' }}</text>
          </view>
          
          <!-- ÂõæÁâáÂ±ïÁ§∫ -->
          <view class="post-images" v-if="hasImages(post)" :class="'cols-' + gridCols(post)">
            <image v-for="(img, idx) in getImages(post)"
                   :key="idx"
                   :src="getImageUrl(img)"
                   class="post-image"
                   mode="aspectFill"
                   @error="onImageError" />
          </view>
          
          <!-- Â∏ñÂ≠ê‰ø°ÊÅØ -->
          <view class="post-info">
            <text class="post-time">{{ displayTime(post) }}</text>
            <view class="post-stats">
              <view class="stat">
                <text class="heart-icon" :class="{ 'liked': post.liked }">‚ù§</text>
                <text class="stat-count">{{ post.likes || 0 }}</text>
              </view>
              <view class="stat">
                <text class="comment-icon">üí¨</text>
                <text class="stat-count">{{ getCommentCount(post.id) }}</text>
              </view>
            </view>
          </view>
        </view>
      </view>
    </view>

    <view style="height: 40rpx;" />
  </view>
</template>

<script>
import { 
  getCommunityPosts, 
  getUserInfo, 
  followUser, 
  unfollowUser, 
  getFollowStats, 
  checkIsFollowed,
  getFollowingList,
  getFollowersList
} from '@/api/recipes';
import { getUserInfoById } from '@/api/user';

export default {
  data() {
    return {
      userInfo: {
        id: '',
        name: '',
        avatar: '',
        bio: '',
        followCount: 0,
        fansCount: 0,
        likeCount: 0,
        isFollowed: false
      },
      posts: [],
      loading: false,
      commentsMap: {},
      isCurrentUser: false
    }
  },
  
  async onLoad(options) {
    console.log('Áî®Êà∑ÁÆÄ‰ªãÈ°µÈù¢ÂèÇÊï∞:', options);
    await this.loadUserInfo(options);
    this.loadUserPosts(options.userId);
  },
  
  methods: {
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    async loadUserInfo(options) {
      const { userId, userName, userAvatar } = options;
      
      try {
        // Ëß£Á†ÅURLÂèÇÊï∞ÔºàÁâπÂà´ÊòØÂ§¥ÂÉèURLÔºâ
        const decodedUserAvatar = userAvatar ? decodeURIComponent(userAvatar) : '/static/picture/profile.png';
        const decodedUserName = userName ? decodeURIComponent(userName) : 'ÁæéÈ£üËææ‰∫∫';
        
        console.log('Ëß£Á†ÅÂêéÁöÑÁî®Êà∑‰ø°ÊÅØ:', {
          userId,
          userName: decodedUserName,
          userAvatar: decodedUserAvatar
        });
        
        // ‰ºòÂÖà‰ΩøÁî®URLÂèÇÊï∞‰Ωú‰∏∫ÈªòËÆ§ÂÄºÔºåÁÑ∂ÂêéÈÄöËøáAPIËé∑ÂèñÁúüÂÆûÊï∞ÊçÆ
        this.userInfo = {
          id: userId || '',
          name: decodedUserName,
          avatar: decodedUserAvatar,
          bio: 'ÁÉ≠Áà±ÁæéÈ£üÔºåÂàÜ‰∫´ÁîüÊ¥ª',
          followCount: 0,
          fansCount: 0,
          likeCount: 0,
          isFollowed: false
        };
        
        // Ê£ÄÊü•ÊòØÂê¶ÊòØÂΩìÂâçÁî®Êà∑
        this.checkIfCurrentUser(userId);
        
        // ÈÄöËøáAPIËé∑ÂèñÁúüÂÆûÁöÑÁî®Êà∑‰ø°ÊÅØ
        if (userId) {
          await this.loadRealUserInfo(userId);
        }
        
        // Ëé∑ÂèñÂÖ≥Ê≥®ÁªüËÆ°‰ø°ÊÅØ
        await this.loadFollowStats();
        
        // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÂÖ≥Ê≥®ËØ•Áî®Êà∑
        if (userId && !this.isCurrentUser) {
          await this.checkFollowStatus(userId);
        }
        
      } catch (error) {
        console.error('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error);
        // Â¶ÇÊûúAPIË∞ÉÁî®Â§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÂÄº
        this.userInfo.followCount = 0;
        this.userInfo.fansCount = 0;
        this.userInfo.likeCount = 0;
      }
    },
    
    // ÈÄöËøáAPIËé∑ÂèñÁúüÂÆûÁöÑÁî®Êà∑‰ø°ÊÅØ
    async loadRealUserInfo(userId) {
      try {
        const userData = await getUserInfoById(userId);
        console.log('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØAPIÂìçÂ∫î:', userData);
        
        // Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØ‰∏∫APIËøîÂõûÁöÑÁúüÂÆûÊï∞ÊçÆ
        this.userInfo.name = userData.nickname || this.userInfo.name;
        this.userInfo.avatar = userData.avatar || this.userInfo.avatar;
        this.userInfo.bio = userData.bio || userData.introduction || this.userInfo.bio;
        
        console.log('Êõ¥Êñ∞ÂêéÁöÑÁî®Êà∑‰ø°ÊÅØ:', this.userInfo);
      } catch (error) {
        console.error('Ë∞ÉÁî®Áî®Êà∑‰ø°ÊÅØAPIÂ§±Ë¥•:', error);
        // APIË∞ÉÁî®Â§±Ë¥•Êó∂ÔºåÁªßÁª≠‰ΩøÁî®URLÂèÇÊï∞ÁöÑÂÄº
      }
    },
    
    // Ê£ÄÊü•ÊòØÂê¶ÊòØÂΩìÂâçÁî®Êà∑
    checkIfCurrentUser(userId) {
      try {
        // ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÂΩìÂâçÁî®Êà∑‰ø°ÊÅØ
        const currentUser = uni.getStorageSync('userInfo');
        if (currentUser && currentUser.id) {
          this.isCurrentUser = String(currentUser.id) === String(userId);
        } else {
          this.isCurrentUser = false;
        }
        console.log('Ê£ÄÊü•ÊòØÂê¶ÊòØÂΩìÂâçÁî®Êà∑:', { currentUserId: currentUser?.id, targetUserId: userId, isCurrentUser: this.isCurrentUser });
      } catch (error) {
        console.error('Ê£ÄÊü•ÂΩìÂâçÁî®Êà∑Â§±Ë¥•:', error);
        this.isCurrentUser = false;
      }
    },
    
    // Ëé∑ÂèñÂÖ≥Ê≥®ÁªüËÆ°‰ø°ÊÅØ
    async loadFollowStats() {
      try {
        const response = await getFollowStats();
        console.log('Ëé∑ÂèñÂÖ≥Ê≥®ÁªüËÆ°ÂìçÂ∫î:', response);
        
        if (response && response.success) {
          const stats = response.data || {};
          this.userInfo.followCount = stats.followingCount || 0; // ÂÖ≥Ê≥®Êï∞
          this.userInfo.fansCount = stats.followerCount || 0;     // Á≤â‰∏ùÊï∞
          this.userInfo.likeCount = stats.likeCount || 0;        // Ëé∑ËµûÊï∞
        } else {
          console.warn('Ëé∑ÂèñÂÖ≥Ê≥®ÁªüËÆ°Â§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÂÄº');
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÂÖ≥Ê≥®ÁªüËÆ°Â§±Ë¥•:', error);
      }
    },
    
    // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÂÖ≥Ê≥®ËØ•Áî®Êà∑
    async checkFollowStatus(targetId) {
      try {
        const response = await checkIsFollowed(targetId);
        console.log('Ê£ÄÊü•ÂÖ≥Ê≥®Áä∂ÊÄÅÂìçÂ∫î:', response);
        
        if (response && response.success) {
          this.userInfo.isFollowed = response.data || false;
        } else {
          console.warn('Ê£ÄÊü•ÂÖ≥Ê≥®Áä∂ÊÄÅÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÂÄº');
          this.userInfo.isFollowed = false;
        }
      } catch (error) {
        console.error('Ê£ÄÊü•ÂÖ≥Ê≥®Áä∂ÊÄÅÂ§±Ë¥•:', error);
        this.userInfo.isFollowed = false;
      }
    },
    
    // Âä†ËΩΩÁî®Êà∑Â∏ñÂ≠ê
    async loadUserPosts(userId) {
      this.loading = true;
      try {
        // Ëé∑ÂèñËØ•Áî®Êà∑ÁöÑÂ∏ñÂ≠êÂàóË°®
        const response = await getCommunityPosts(1, 20, null, userId);
        console.log('Ëé∑ÂèñÁî®Êà∑Â∏ñÂ≠êÂìçÂ∫î:', response);
        
        if (response && response.list) {
          this.posts = response.list.map(post => ({
            ...post,
            liked: false // ÂàùÂßãÂåñÁÇπËµûÁä∂ÊÄÅ
          }));
          
          // ÊÅ¢Â§çÊî∂ËóèÁä∂ÊÄÅ
          this.restoreLikedStatus(this.posts);
        } else {
          this.posts = [];
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÁî®Êà∑Â∏ñÂ≠êÂ§±Ë¥•:', error);
        uni.showToast({ title: 'Ëé∑ÂèñÂ∏ñÂ≠êÂàóË°®Â§±Ë¥•', icon: 'none' });
        this.posts = [];
      } finally {
        this.loading = false;
      }
    },
    
    // ËøîÂõû‰∏ä‰∏ÄÈ°µ
    goBack() {
      uni.navigateBack();
    },
    
    // ÊòæÁ§∫ÂÖ≥Ê≥®ÂàóË°®
    async showFollowList() {
      // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÁôªÂΩï
      const token = uni.getStorageSync('token');
      if (!token) {
        uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' });
        return;
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÊü•ÁúãÂΩìÂâçÁî®Êà∑ÁöÑÂÖ≥Ê≥®ÂàóË°®
      if (this.isCurrentUser) {
        try {
          const response = await getFollowingList();
          console.log('ÂÖ≥Ê≥®ÂàóË°®ÂìçÂ∫î:', response);
          
          if (response && response.success && response.data) {
            const followingList = response.data.list || [];
            uni.showToast({ title: `ÂÖ≥Ê≥®ÂàóË°®: ${followingList.length}‰∫∫`, icon: 'none' });
          } else {
            uni.showToast({ title: 'Ëé∑ÂèñÂÖ≥Ê≥®ÂàóË°®Â§±Ë¥•', icon: 'none' });
          }
        } catch (error) {
          console.error('Ëé∑ÂèñÂÖ≥Ê≥®ÂàóË°®Â§±Ë¥•:', error);
          uni.showToast({ title: 'Ëé∑ÂèñÂÖ≥Ê≥®ÂàóË°®Â§±Ë¥•', icon: 'none' });
        }
      } else {
        // Êü•ÁúãÂÖ∂‰ªñÁî®Êà∑ÁöÑÂÖ≥Ê≥®ÂàóË°®ÔºåÁõÆÂâçAPI‰∏çÊîØÊåÅÔºåÊèêÁ§∫Áî®Êà∑
        uni.showToast({ title: 'Êü•Áúã‰ªñ‰∫∫ÂÖ≥Ê≥®ÂàóË°®ÂäüËÉΩÊöÇÊú™ÂºÄÊîæ', icon: 'none' });
      }
    },
    
    // ÊòæÁ§∫Á≤â‰∏ùÂàóË°®
    async showFansList() {
      // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÁôªÂΩï
      const token = uni.getStorageSync('token');
      if (!token) {
        uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' });
        return;
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÊü•ÁúãÂΩìÂâçÁî®Êà∑ÁöÑÁ≤â‰∏ùÂàóË°®
      if (this.isCurrentUser) {
        try {
          const response = await getFollowersList();
          console.log('Á≤â‰∏ùÂàóË°®ÂìçÂ∫î:', response);
          
          if (response && response.success && response.data) {
            const followersList = response.data.list || [];
            uni.showToast({ title: `Á≤â‰∏ùÂàóË°®: ${followersList.length}‰∫∫`, icon: 'none' });
          } else {
            uni.showToast({ title: 'Ëé∑ÂèñÁ≤â‰∏ùÂàóË°®Â§±Ë¥•', icon: 'none' });
          }
        } catch (error) {
          console.error('Ëé∑ÂèñÁ≤â‰∏ùÂàóË°®Â§±Ë¥•:', error);
          uni.showToast({ title: 'Ëé∑ÂèñÁ≤â‰∏ùÂàóË°®Â§±Ë¥•', icon: 'none' });
        }
      } else {
        // Êü•ÁúãÂÖ∂‰ªñÁî®Êà∑ÁöÑÁ≤â‰∏ùÂàóË°®ÔºåÁõÆÂâçAPI‰∏çÊîØÊåÅÔºåÊèêÁ§∫Áî®Êà∑
        uni.showToast({ title: 'Êü•Áúã‰ªñ‰∫∫Á≤â‰∏ùÂàóË°®ÂäüËÉΩÊöÇÊú™ÂºÄÊîæ', icon: 'none' });
      }
    },
    
    // ÊòæÁ§∫ÁÇπËµûÂàóË°®
    showLikesList() {
      uni.showToast({ title: 'Êü•ÁúãËé∑ËµûÂàóË°®', icon: 'none' });
    },
    
    // ÂàáÊç¢ÂÖ≥Ê≥®Áä∂ÊÄÅ
    async toggleFollow() {
      if (!this.userInfo.id) {
        uni.showToast({ title: 'Áî®Êà∑‰ø°ÊÅØ‰∏çÂÆåÊï¥', icon: 'none' });
        return;
      }
      
      try {
        uni.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' });
        
        if (this.userInfo.isFollowed) {
          // ÂèñÊ∂àÂÖ≥Ê≥®
          const response = await unfollowUser(this.userInfo.id);
          if (response && response.success) {
            this.userInfo.isFollowed = false;
            this.userInfo.fansCount = Math.max(0, this.userInfo.fansCount - 1);
            uni.showToast({ title: 'Â∑≤ÂèñÊ∂àÂÖ≥Ê≥®', icon: 'success' });
          } else {
            throw new Error('ÂèñÊ∂àÂÖ≥Ê≥®Â§±Ë¥•');
          }
        } else {
          // ÂÖ≥Ê≥®Áî®Êà∑
          const response = await followUser(this.userInfo.id);
          if (response && response.success) {
            this.userInfo.isFollowed = true;
            this.userInfo.fansCount++;
            uni.showToast({ title: 'ÂÖ≥Ê≥®ÊàêÂäü', icon: 'success' });
          } else {
            throw new Error('ÂÖ≥Ê≥®Â§±Ë¥•');
          }
        }
      } catch (error) {
        console.error('ÂàáÊç¢ÂÖ≥Ê≥®Áä∂ÊÄÅÂ§±Ë¥•:', error);
        uni.showToast({ title: 'Êìç‰ΩúÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
      } finally {
        uni.hideLoading();
      }
    },
    
    // ÂèëÈÄÅÁßÅ‰ø°
    sendMessage() {
      uni.showToast({ title: 'ÁßÅ‰ø°ÂäüËÉΩÂºÄÂèë‰∏≠', icon: 'none' });
    },
    
    // Êü•ÁúãÂ∏ñÂ≠êËØ¶ÊÉÖ
    viewPostDetail(post) {
      uni.showToast({ title: 'Êü•ÁúãÂ∏ñÂ≠êËØ¶ÊÉÖ', icon: 'none' });
      // ËøôÈáåÂèØ‰ª•Ë∑≥ËΩ¨Âà∞Â∏ñÂ≠êËØ¶ÊÉÖÈ°µ
    },
    
    // ÂõæÁâáÁõ∏ÂÖ≥ÊñπÊ≥ï
    hasImages(post) {
      return !!(post && this.getImages(post).length > 0);
    },
    
    gridCols(post) {
      const len = this.getImages(post).length;
      if (len <= 1) return 1;
      if (len === 2) return 2;
      return 3;
    },
    
    // Ëé∑ÂèñÂõæÁâáÊï∞ÁªÑ
    getImages(post) {
      if (!post) return [];
      
      // Â§ÑÁêÜ mediaJson Â≠óÊÆµ
      if (post.mediaJson) {
        try {
          const media = JSON.parse(post.mediaJson);
          if (Array.isArray(media)) {
            return media.map(item => {
              if (typeof item === 'string') return item;
              if (item && item.url) return item.url;
              return '';
            }).filter(url => url);
          }
        } catch (e) {
          console.error('Ëß£Êûê mediaJson Â§±Ë¥•:', e);
        }
      }
      
      // Â§ÑÁêÜ images Â≠óÊÆµ
      if (Array.isArray(post.images)) {
        return post.images.filter(url => url);
      }
      
      return [];
    },
    
    // Â§ÑÁêÜÂõæÁâáURL
    getImageUrl(img) {
      if (!img) return '';
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÊúâÊïàÁöÑURL
      if (img.startsWith('http://') || img.startsWith('https://')) {
        return img;
      }
      
      // Â§ÑÁêÜÂæÆ‰ø°Â∞èÁ®ãÂ∫è‰∏¥Êó∂Êñá‰ª∂Ë∑ØÂæÑ
      if (img.startsWith('http://tmp/')) {
        return img.replace('http://tmp/', 'wxfile://tmp/');
      }
      
      // Â§ÑÁêÜÂºÄÂèëÊúçÂä°Âô®‰∏¥Êó∂Êñá‰ª∂Ë∑ØÂæÑ
      if (img.startsWith('http://127.0.0.1')) {
        return '/static/picture/profile.png';
      }
      
      // Â§ÑÁêÜwxfileÂçèËÆÆË∑ØÂæÑ
      if (img.startsWith('wxfile://')) {
        return img;
      }
      
      // Â§ÑÁêÜdata URIÂõæÁâá
      if (img.startsWith('data:image/')) {
        return img;
      }
      
      // Â§ÑÁêÜÁõ∏ÂØπË∑ØÂæÑ
      if (img.startsWith('/')) {
        return img;
      }
      
      return img;
    },
    
    // Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•Â§ÑÁêÜ
    onAvatarError(e) {
      console.warn('Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•:', e.detail);
      // ÂΩìÂ§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•Êó∂Ôºå‰ΩøÁî®ÈªòËÆ§Â§¥ÂÉè
      this.userInfo.avatar = '/static/picture/profile.png';
    },
    
    // Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•Â§ÑÁêÜ
    onAvatarError(e) {
      console.warn('Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•:', e.detail);
      // ÂΩìÂ§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•Êó∂Ôºå‰ΩøÁî®ÈªòËÆ§Â§¥ÂÉè
      this.userInfo.avatar = '/static/picture/profile.png';
    },
    
    // ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•Â§ÑÁêÜ
    onImageError(e) {
      console.warn('ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•:', e.detail);
      const imageElement = e.target || e.currentTarget;
      if (imageElement) {
        imageElement.src = '/static/picture/profile.png';
      }
    },
    
    // ÊòæÁ§∫Êó∂Èó¥
    displayTime(post) {
      const t = post && post.time;
      if (!t) return 'ÂàöÂàö';
      const s = String(t).trim().toLowerCase();
      if (!s || s === 'null' || s === 'undefined') return 'ÂàöÂàö';
      return t;
    },
    
    // Ëé∑ÂèñËØÑËÆ∫Êï∞Èáè
    getCommentCount(pid) {
      const cm = this.commentsMap || {};
      const list = cm[String(pid)] || [];
      return Array.isArray(list) ? list.length : 0;
    },
    
    // ‰ªéÊú¨Âú∞Â≠òÂÇ®ÊÅ¢Â§çÊî∂ËóèÁä∂ÊÄÅ
    restoreLikedStatus(posts) {
      try {
        const likedPosts = uni.getStorageSync('liked_posts') || {};
        posts.forEach(post => {
          if (post && post.id) {
            const postId = String(post.id);
            if (likedPosts[postId]) {
              this.$set(post, 'liked', true);
            }
          }
        });
      } catch (error) {
        console.error('ÊÅ¢Â§çÊî∂ËóèÁä∂ÊÄÅÂ§±Ë¥•:', error);
      }
    }
  }
}
</script>

